<template>
  <div class="role-config business-portal-page">
    <!-- title -->
    <portal-header :projectObj="titleConfig" :showBottomLine="false" />
    <!--search -->
    <portal-form
      ref="searchList"
      :form-data="searchList"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      :formRules="queryFormRules"
      :bigAutoColumn="3"
      @common-enter-event="search"
      @reset-btn-click="reset"
      @search-btn-click="search"
    >
      <template slot="time">
        <el-form-item class="form-item">
          <span slot="label"
            >Time
            <span class="label-tips">{{ $t('IDC.auditlog.tips') }}</span>
          </span>
          <el-date-picker v-bind="timeDateConfig" />
          <i class="el-icon-date data_icon"></i>
        </el-form-item>
      </template>
    </portal-form>
    <!-- columns -->
    <div class="table-list-container">
      <div style="padding-bottom: 16px">
        <el-dropdown @command="exportData">
          <el-button type="primary" size="small" plain v-preventReClick>
            {{ $t('common.button.export') }}
            <i class="el-icon-arrow-down el-icon--right" style="font-size: 12px"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="XLS">Excel</el-dropdown-item>
            <el-dropdown-item command="XLSX">Excel 2007</el-dropdown-item>
            <el-dropdown-item command="CSV">CSV</el-dropdown-item>
            <el-dropdown-item command="PDF">PDF</el-dropdown-item>
            <el-dropdown-item command="TXT">TXT</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-button @click.native="handleDetailPopup">detail</el-button>
      </div>
      <table-page
        size="small"
        class="table-page"
        :has-pagination="true"
        :columns="columns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :is-search="refreshTableFlag"
        :sortArr="['time']"
      >
        <template slot="operation" slot-scope="scope">
          <el-button type="text" class="btn" @click="detail(scope.data.row)">
            {{ $t('common.label.detail') }}
          </el-button>
        </template>
      </table-page>
    </div>
    <view-detail-popup ref="popup-service" :popupVisible.sync="popupVisible"></view-detail-popup>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import ViewDetailPopup from '../components/view-detail-popup';
import dayjs from 'dayjs';
import { exportUtils } from '@/common/utils/export-utils';
import { downloadAuditLog } from '@/views/identifier-center/api/api-audit-log';
export default {
  components: { PortalHeader, TablePage, PortalForm, ViewDetailPopup },
  name: 'AuditLog',
  data() {
    return {
      timeDateConfig: {
        align: 'right',
        type: 'datetimerange',
        format: 'dd/MM/yyyy HH:mm:ss',
        'range-separator': '~',
        'value-format': 'yyyy-MM-dd HH:mm:ss',
        'default-time': ['00:00:00', '23:59:59'],
        'start-placeholder': 'Please select',
        'end-placeholder': 'Please select',
      },
      titleConfig: {
        title: this.$t('IDC.auditlog.AuditLogTitle'),
        desc: this.$t('IDC.auditlog.desc'),
      },
      searchLoading: false,
      refreshTableFlag: false,
      popupVisible: false,
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      searchList: {
        category: '',
        time: '',
        event: '',
      },
      categoryList: [
        {
          value: '20',
          label: 'Mutually Exclusive',
        },
        {
          value: '21',
          label: 'query',
        },
      ],
      eventList: [
        {
          value: '1',
          label: 'Mutually Exclusive',
        },
        {
          value: '2',
          label: 'query',
        },
        {
          value: '3',
          label: 'group',
        },
      ],
      columns: [],
      columnsA: [
        { label: this.$t('IDC.auditlog.initiatorType'), prop: 'initiatorType', width: 120, align: 'left' },
        { label: this.$t('IDC.auditlog.initiator'), prop: 'initiator', width: 100, align: 'left' },
        { label: this.$t('IDC.auditlog.time'), prop: 'time', width: 100, align: 'left', __slotName: 'time' },
        { label: this.$t('IDC.auditlog.event'), prop: 'event', align: 'left' },
        { label: this.$t('IDC.auditlog.identity'), prop: 'identity', align: 'left' },
        { label: this.$t('IDC.auditlog.transactionType'), prop: 'transactionType', width: 140, align: 'left' },
        { label: this.$t('IDC.auditlog.reasonType'), prop: 'reasonType', width: 120, align: 'left' },
        { label: this.$t('IDC.auditlog.tradeOrderId'), prop: 'tradeOrderId', width: 130, align: 'left' },
        {
          label: this.$t('IDC.auditlog.operation'),
          prop: 'operation',
          width: 100,
          align: 'left',
          __slotName: 'operation',
        },
      ],
      columnsB: [
        { label: this.$t('IDC.auditlog.initiatorType'), prop: 'initiatorType', align: 'left' },
        { label: this.$t('IDC.auditlog.initiator'), prop: 'initiator', align: 'left' },
        { label: this.$t('IDC.auditlog.time'), prop: 'time', align: 'left', __slotName: 'time' },
        { label: this.$t('IDC.auditlog.event'), prop: 'event', align: 'left' },
        { label: this.$t('IDC.auditlog.identity'), prop: 'identity', align: 'left' },
        { label: this.$t('IDC.auditlog.transactionType'), prop: 'transactionType', align: 'left' },
      ],
      columnsC: [
        { label: this.$t('IDC.auditlog.time'), prop: 'time', align: 'left', __slotName: 'time' },
        { label: this.$t('IDC.auditlog.event'), prop: 'event', align: 'left' },
        { label: this.$t('IDC.auditlog.identity'), prop: 'identity', align: 'left' },
        { label: this.$t('IDC.auditlog.transactionType'), prop: 'transactionType', align: 'left' },
        { label: this.$t('IDC.auditlog.reasonType'), prop: 'reasonType', align: 'left' },
        { label: this.$t('IDC.auditlog.tradeOrderId'), prop: 'tradeOrderId', align: 'left' },
        { label: this.$t('IDC.auditlog.operation'), prop: 'operation', align: 'left', __slotName: 'operation' },
      ],
      drawer: false,
      direction: 'rtl',
      drawerForm: {
        organization: '0040 - AgencyHeadOffice10040',
        commissionPeriod: '0040 - AgencyHeadOffice10040',
        accountType: 'Org Commission Received Account_VDF_Demo',
        accountNumber: '0040 - AgencyHeadOffice10040',
        currency: '0040 - AgencyHeadOffice10040',
        subtotal: '10.00',
        status: 'Created',
        settlementMode: 'Manually by Inputting Amount',
        settlementTime: '01/06/2022 00:00:00',
        operator: 'MM SP',
        lastUpdateTime: '01/06/2022 00:00:00',
        remark: '-',
      },
      queryFormRules: {
        time: [{ required: true, message: 'Please select time', trigger: 'change' }],
      },
    };
  },
  computed: {
    formConfigList() {
      return [
        {
          label: this.$t('IDC.auditlog.category'),
          key: 'category',
          comp: 'el-select',
          options: this.categoryList,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          label: this.$t('IDC.auditlog.groupEvent'),
          key: 'groupEvent',
          comp: 'el-select',
          options: this.eventList,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
          event: {
            change: (val) => {
              switch (val) {
                case '1':
                  this.columns = this.columnsA;
                  break;
                case '2':
                  this.columns = this.columnsB;
                  break;
                case '3':
                  this.columns = this.columnsC;
                  break;
              }
            },
          },
          hidden: false,
        },
        {
          label: this.$t('IDC.auditlog.event'),
          key: 'event',
          comp: 'el-select',
          options: this.eventList,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
          hidden: false,
        },
        {
          prop: 'time',
          slotName: 'time',
        },
      ];
    },
  },
  methods: {
    reset() {
      this.refreshTableFlag = true;
      this.searchList = {};
      this.paginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      };
    },
    search() {
      this.$refs['searchList'].validate((isValid) => {
        if (isValid) {
          this.refreshTableFlag = true;
          this.paginationConfig = {
            total: 0,
            pageIndex: 1,
            pageSize: 10,
          };
        }
      });
    },
    exportData(fileType) {
      //   后期导出使用
      let paramObj = Object.assign(this.searchList, {
        startTime: dayjs(this.searchList.time[0]).format('YYYY-MM-DD HH:mm:ss') || '2022-07-01 00:00:00',
        endTime: dayjs(this.searchList.time[1]).format('YYYY-MM-DD HH:mm:ss') || '2022-08-03 23:59:59',
        pageNo: this.paginationConfig.pageIndex,
        pageSize: this.paginationConfig.pageSize,
        type: 'FINANCIAL_RECONCILIATION',
        fileType: fileType,
      });
      // 暂时移植别的接口过来规避注释代码规范问题
      exportUtils(downloadAuditLog, paramObj, { fileType });
    },
    detail() {
      this.drawer = true;
    },
    handleDetailPopup() {
      this.popupVisible = true;
    },
    handleClose(done) {
      done();
    },
    getTableData() {
      let res = {};
      return new Promise((resolve) => {
        res['list'] = [
          {
            initiatorType: 'SP Operator',
            initiator: 'MM SP0000',
            time: '03/09/2022 10:25:48',
            event: 'Change Group Member',
            identity: 'Forex200',
          },
        ];
        res['totalRecords'] = 1;
        this.refreshTableFlag = false;
        this.searchLoading = false;
        res['header']['success'] = true;
        resolve(res);
      });
    },
  },
  created() {
    this.columns = this.columnsA;
  },
};
</script>

<style scoped lang="scss">
/deep/ .el-drawer__body {
  padding-left: 20px;
}
.label-tips {
  color: $--line_color--dark;
}
</style>
