<template>
  <div class="initiatePopup">
    <el-dialog
      :title="$t('IDC.auditlog.viewDetails')"
      :visible.sync="popupVisible"
      width="560px"
      :close-on-click-modal="false"
      :before-close="cancel"
    >
      <el-descriptions
        class="margin-top"
        :column="1"
        :border="false"
        :colon="false"
        size="medium"
        :label-style="labelStyle"
        :content-style="contentStyle"
      >
        <template>
          <el-descriptions-item v-for="(item, index) in viewDetailList" :key="index" :label="item.label">
            {{ item.content }}
          </el-descriptions-item>
        </template>
      </el-descriptions>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'addPrompt',
  components: {},
  props: {
    popupVisible: {
      type: Boolean,
      default: false,
    },
    addForm: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      viewDetailList: [
        { label: 'Initiator:', key: 'initiator' },
        { label: 'Time:', key: 'time' },
        { label: 'Event:', key: 'event' },
        { label: 'Start Time:', key: 'startTime' },
        { label: 'End Time:', key: 'endTime' },
        { label: 'Search Criteria:', key: 'searchCriteria' },
        { label: 'Has Been Downloaded:', key: 'downloaded' },
        { label: 'Is Sensitive:', key: 'isSensitive' },
        { label: 'Info Type:', key: 'infoType' },
        { label: 'Remark:', key: 'remark' },
        { label: 'Client IP:', key: 'clientIp' },
        { label: 'Event Result:', key: 'eventResult' },
      ],
      labelStyle: {
        'font-size': '14px',
        color: 'var(--line_color--dark)',
        'padding-bottom': '5px',
        width: '149px',
      },
      contentStyle: {
        'font-size': '14px',
        color: 'var(--text_color--emphasis)',
        'padding-bottom': '20px',
      },
      row: {
        // 假数据
        initiator: 'Top10021002020',
        time: '31/08/2022 16:00:30',
        event: 'View Till Details',
        startTime: '31/08/2022 16:00:30',
        endTime: '31/08/2022 16:00:30',
        searchCriteria: 'Till ID:20400000000020030',
        downloaded: 'No',
        isSensitive: 'Yes',
        infoType: 'Till Details',
        remark: 'remark',
        clientIp: '10.234.55.13',
        eventResult: 'Success',
      },
    };
  },
  computed: {},
  methods: {
    init() {
      this.viewDetailList = this.viewDetailList.map((item) => ({
        label: item.label,
        content: this.row[item.key] || '-',
      }));
    },
    cancel() {
      this.$emit('update:popupVisible', false);
    },
  },
  created() {},
  mounted() {},
  activated() {
    this.init();
  },
};
</script>

